import * as React from "react";
import styled from "styled-components";
import Post from "@components/Post";
import { device } from "@layout/styleConfig";

const Layout = styled.div.withConfig({ componentId: "Project_Layout" })`
  display: grid;
  grid-template-columns: repeat(4, minmax(0, 1fr));

  @media ${device.tablet} {
    grid-template-columns: repeat(8, minmax(0, 1fr));
  }

  @media ${device.laptop} {
    margin: 0 10rem;
    grid-template-columns: repeat(12, minmax(0, 1fr));
  }
`;

export default function Project({ list = [] }) {
  return (
    <Layout>
      {list.map((post, index) => {
        const POST_DATA = post.frontmatter;
        return <Post key={index} data={POST_DATA} />;
      })}
    </Layout>
  );
}
